import {
  React,
  connect,
  reduxForm,
  Component,
  Field,
  ReanderField,
  NumberOnly,
} from "../../../../components/helpers/library";

class TambahBarangBanyak extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    setTimeout(() => {
      document.getElementById("jml_barang").focus();
    }, 100);
  }
  render() {
    return (
      <>
        <form onSubmit={this.props.handleSubmit}
           autoComplete="off"
           onKeyPress={(e) => {
             e.key === "Enter" && e.preventDefault();
           }}
        >
          <div className="row">
            <div className="col-lg-6">
              <Field
                id="jml_barang"
                normalize={NumberOnly}
                name="jml_barang"
                component={ReanderField}
                label="Jumlah Tambah"
                placeholder="Masukan Jumlah Barang"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kode_barang_awal"
                component={ReanderField}
                label="Kode Barang Awal"
                readOnly={true}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kode_barang_akhir"
                component={ReanderField}
                label="Kode Barang Akhir"
                readOnly={true}
              />
            </div>
            <div className="col-lg-6">
              <Field
                name="nama_barang"
                component={ReanderField}
                label="Nama Barang"
                placeholder="Masukan Nama Barang"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kode_barcode_awal"
                component={ReanderField}
                label="Kode Barcode Awal"
                readOnly={true}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kode_barcode_akhir"
                component={ReanderField}
                label="Kode Barcode Akhir"
                readOnly={true}
              />
            </div>
            <div className="col-lg-6 text-right"></div>
            <div className="col-lg-6 text-right">
              <button type="button" className="btn btn-primary mt-1">
                <i className="fa fa-plus"></i> Simpan{" "}
              </button>{" "}
              &nbsp;
              <button
                type="button"
                onClick={() => this.props.FormBatal()}
                className="btn btn-warning mt-1"
              >
                Batal{" "}
              </button>{" "}
            </div>
          </div>
        </form>
      </>
    );
  }
}

TambahBarangBanyak = reduxForm({
  form: "TambahBarangBanyak",
  enableReinitialize: true,
})(TambahBarangBanyak);
export default connect()(TambahBarangBanyak);
