import {
  React,
  imgfound,
  connect,
  ReanderField,
  createNumberMask,
  Field,
  reduxForm,
  Camera,
  Component,
  PanelHeader,
  Panel,
  ReanderSelect,
  Gramasi,
  ModalGlobal,
  showModal,
  hideModal,
  reset,
  localStorageencryp,
  getData,
  PanelBody,
  getDataNoTimeOut2,
  Loading,
  getData2,
} from "../../../../components/helpers/library";
import { io } from "socket.io-client";

// import { showModalWebcam } from "../../../store/actions/Barang/dataBarang_action";
import TambahBarangBanyak from "./TambahBarangBanyak";

// import { setCamera, tampilModalWebCam } from "./ModuleDataBarang";
import FormDataAtribut from "./FormDataAtribut";
import {
  NotifSucces,
  ToastNotification,
} from "../../../../components/helpers/notification";
import {
  getDataBaki,
  GetDataGroup,
  getDataGudang,
  getDataJenis,
  getMasterAtribut,
  // getMasterLabeling,
  getMasterPabrik,
  showModalWebcam,
} from "../../../../actions/datamaster_action";
import { setCamera } from "../../TambahBarang/Module";
import {
  getItem,
  getUserdata,
  setItem,
} from "../../../../components/helpers/function";
import ValidasiTambahBarangPusat from './../../../../Validasi/Barang/ValidasiTambahBarangPusat';


const maptostate = (state) => {
  return {
    modalwebcam: state.datamaster.showModalWebcam,
    foto: state.databarang.showEditBarang.foto,
    isLoading: state.datamaster.isLoading,
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
    // DataLabeling: state.datamaster.getMasterLabeling,
    DataGroup: state.datamaster.GetDataGroup,
    DataJenis: state.datamaster.getDataJenis,
    DataGudang: state.datamaster.getDataGudang,
    DataBaki: state.datamaster.getDataBaki,
    initialValues: {
      is_markis: "FALSE"
    },
  };
};
const server = process.env.REACT_APP_BACKEND_URL;

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
class FormTambahDataBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      ModalWebCam: false,
      isLoadingBaki: false,
      isLoadingJenis: false,
      showModalForm: false,
      loadingData: false,
      cekStatus: false,
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
      databaki: [],
      DataLabeling: [],
      DataGroup: [],
      DataJenis: [],
      loadimage: imgfound,
      name_type: "SP",
      kode_gudang :"TOKO",
      stock_input : "NISA1"
    };
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  handleTakePhoto(url) {
    this.props.dispatch(setCamera(url));
    this.props.change("foto", url);
  }
  shofFormModal(id) {
    this.setState({
      showModalForm: id,
    });
    this.props.dispatch(showModal());
  }

  getDataDetailBarangBos(params) {
    if(params.length >= 14){

    if(this.delayTimer){
      clearTimeout(this.delayTimer);
    }

    this.delayTimer = setTimeout(() => {
      this.setState({ loadingData: true });

      if (this.state.stock_input === undefined) {
        this.setState({ loadingData: false });
        ToastNotification("info", "Kode Baki Belum Dipilih");
        return false;
      }
      this.setState({
        no_kirim: params.trim(),
      });
      getDataNoTimeOut2(
        "kirim-labeling/jenises?no_kirim=" +
          params.trim() +
          "&kode_toko=" +
          this.state.stock_input
      )
        .then((res) => {
          if (res.data.length === 0) {
            this.setState({
              loadingData: false,
            });
            ToastNotification("info", "No Kirim Barang Sudah Tidak Ada");
          } else {
            let hasil = [];
            res.data.forEach((element) => {
              let row = {
                value: element.kode_dept,
                name: element.kode_dept,
              };
              hasil.push(row);
            });
            let nama_barang = this.props.DataJenis.find((list)=>list.kode_dept ===res.data[0].kode_dept);
            
            // console.log(nama_barang)
            this.props.change("jenis_bos",res.data[0].kode_dept)
            this.props.change("nama_barang",nama_barang.nama_dept)
            getData2(
              "kirim-labeling?kode_toko=" +
                this.state.stock_input +
                "&no_kirim=" +
                this.state.no_kirim +
                "&kode_dept=" +
                res.data[0].kode_dept
            )
              .then((res) => {
                document.getElementById("sales_pabrik_bos").focus();
                this.props.change("kode_group", res.data.kode_group);
                this.props.change("qty_bos", res.data.stock_akhir);
                this.props.change("berat_bos", res.data.berat_akhir.toFixed(3));
                this.setState({
                  name_type : res.data.kadar
                })

                localStorageencryp("kadar_type", res.data.kadar);

                
                let datagroup = this.props.DataGroup.find((list)=>list.kode_group === res.data.kode_group);

                this.props.change('kadar_cetak',datagroup.kadar || 0)

                this.props.change(
                  "sales_pabrik_bos",
                  res.data.sales_pabrik === undefined ? "-" : res.data.sales_pabrik
                );
              })
              .catch((err) => {
                console.log(err);
                ToastNotification(
                  "info",
                  err.response?.data.message || "Terjadi Kesalahan Saat Request Data"
                );
              });
            this.setState({
              DataJenis: hasil,
              loadingData: false,
            });
          }
        })
        .catch((err) => {
          ToastNotification(
            "info",
            err.response?.data.message || "Gagal Mengirim Data Keserver"
          );
          this.setState({
            loadingData: false,
          });
        });
    }, 1500);
  }

  }

  FormBatal(id) {
    if (id === true) {
      this.props.dispatch(hideModal());
      this.props.dispatch(reset("TambahBarangBanyak"));
    } else {
      this.props.dispatch(hideModal());
      this.props.dispatch(reset("FormDataAtribut"));
    }
  }
  resetKamera() {
    this.props.change("foto", "");
    this.props.dispatch(setCamera(false));
    // this.props.dispatch(showModalWebcam(false));
  }
  componentDidMount() {
    this.props.change('stock_input',"NISA1")

    this.props.change('gudang',"TOKO")
    this.props.change('kadar',100)
    const socket = io.connect(server.replace("/api/v1/", ""));
    socket.on("timbangan", (data) => {
      if (getUserdata().user_id === data.user_id) {
        this.props.change("berat_asli", data.value);
      }
    });

    this.props.dispatch(GetDataGroup())
    getData("baki/get/by-kode-gudang/KC").then((res) => {
      // console.log(res.data);
      this.setState({
        DataLabeling: res.data,
      });
    });

    this.props.change("jumlah_barang", 1);
    setItem("dataattr", []);
    localStorageencryp("kadar_type", this.state.name_type);
    localStorageencryp("pabrik", "-");
    this.props.dispatch(getMasterPabrik());
    this.props.dispatch(getMasterAtribut());
    this.props.dispatch(GetDataGroup());
    // this.props.dispatch(getMasterLabeling());
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getDataGudang());
    this.props.dispatch(getDataBaki());
  }

  simpanDataAtrr(val) {
    let hasil = {
      level1: val.level1 || "",
      level2: val.level2 || "",
      level3: val.level3 || "",
      level4: val.level4 || "",
      custome: val.custome || "",
      kadar: val.kadar || "",
      pabrik: val.pabrik || "",
      model: val.model || false,
    };
    setItem("dataattr", hasil);
    NotifSucces("Berhasil Menambahkan Data Attribut !").then(() =>
      this.props.dispatch(hideModal())
    );
    let dataatr = getItem("dataattr");
    let namaatr = "-";
    let mdl = "";
    if (dataatr.model) {
      mdl = "MDL";
    }
    if (dataatr.length !== 0) {
      namaatr =
        mdl +
        " " +
        dataatr.level1 +
        " " +
        dataatr.level2 +
        " " +
        dataatr.level3 +
        " " +
        dataatr.level4 +
        " " +
        dataatr.custome +
        " " +
        dataatr.kadar +
        " " +
        dataatr.pabrik 
    }

    this.props.change("atribut",namaatr)
  }

  onValChange = (event) => {
    this.setState({
      name_type: event.target.value,
    });
    localStorageencryp("kadar_type", event.target.value);
  };

  tampilModalWebCam() {
    this.props.dispatch(setCamera(false));
    // this.props.dispatch(showModal());
    this.props.dispatch(showModalWebcam(true));

    this.setState({ ModalWebCam: true });
  }
  checkQty(e) {
    // console.log(e);
    if (e !== "") {
      let qty = document.getElementById("qty").value;
      if (qty === "") {
        this.setState({
          cekStatus: true,
        });
        ToastNotification("info", "Qty Tidak Boleh Kosong");
        this.props.change("jumlah_barang", 0);
        return false;
      } else {
        this.setState({
          cekStatus: false,
        });
      }
      if (parseInt(e) > parseInt(qty)) {
        this.setState({
          cekStatus: true,
        });
        ToastNotification("info", "Jumlah Tidak Boleh Lebih Dari Qty");
        this.props.change("jumlah_barang", 0);
        return false;
      } else {
        this.setState({
          cekStatus: false,
        });
      }
    }
  }
  carikategori(e) {
    getData2(
      "kirim-labeling?kode_toko=" +
        this.state.stock_input +
        "&no_kirim=" +
        this.state.no_kirim +
        "&kode_dept=" +
        e
    )
      .then((res) => {
        document.getElementById("sales_pabrik_bos").focus();
        this.props.change("kode_group", res.data.kode_group);
        this.props.change("qty_bos", res.data.stock_akhir);
        this.props.change("berat_bos", res.data.berat_akhir.toFixed(3));
        this.props.change(
          "sales_pabrik_bos",
          res.data.sales_parbik === undefined ? "-" : res.data.sales_parbik
        );
      })
      .catch((err) => {
        console.log(err);
        ToastNotification(
          "info",
          err.response?.data.message || "Terjadi Kesalahan Saat Request Data"
        );
      });
  }
  componentDidUpdate(){
    this.props.change('stock_input',"NISA1")
    this.props.change('gudang',"TOKO")
    this.props.change('kadar',100)
  }
  render() {
    return (
      <>
        <Panel>
          <PanelHeader>Filter Pencarian Barang</PanelHeader>
          <PanelBody>
            <form
              onSubmit={this.props.handleSubmit}
              autoComplete="off"
              onKeyPress={(e) => {
                e.key === "Enter" && e.preventDefault();
              }}
            >
              <div className="row">
                <div className="col-lg-8">
                  <div className="row">
                    <div className="col-lg-4">
                      <Field
                        name="gudang"
                        onChange={(e) => this.setState({ kode_gudang: e })}
                        component={ReanderSelect}
                        options={this.props.DataGudang.map((list) => {
                          let data = {
                            value: list.kode_gudang,
                            name: list.kode_gudang,
                          };
                          return data;
                        })}
                        label="Gudang"
                        placeholder="Silahkan Pilih Gudang"
                      />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        name="baki"
                        component={ReanderSelect}
                        options={this.props.DataBaki.filter(
                          (list) => list.kode_gudang === this.state.kode_gudang
                        ).map((list) => {
                          let data = {
                            value: list.kode_baki,
                            name: list.kode_baki,
                          };
                          return data;
                        })}
                        onChange={() =>
                          document.getElementById("nama_barang").focus()
                        }
                        label="Kode Baki"
                        placeholder="Silahkan Pilih Kode Baki"
                      />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        name="kode_group"
                        component={ReanderSelect}
                        options={this.props.DataGroup.map((list) => {
                          let data = {
                            value: list.kode_group,
                            name: list.kode_group + " - " + list.nama_group,
                          };
                          return data;
                        })}
                        onChange={() =>
                          document.getElementById("nama_barang").focus()
                        }
                        label="Kode Group"
                        placeholder="Silahkan Pilih Kode Group"
                      />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        name="kode_jenis"
                        component={ReanderSelect}
                        options={this.props.DataJenis.map((list) => {
                          let data = {
                            value: list.kode_dept,
                            name: list.kode_dept + " - " + list.nama_dept,
                          };
                          return data;
                        })}
                        onChange={() =>
                          document.getElementById("nama_barang").focus()
                        }
                        label="Kode Jenis"
                        placeholder="Silahkan Pilih Kode Jenis"
                      />
                    </div>
                    <div className="col-lg-8"></div>
                    <div className="col-lg-8">
                      <Field
                        id="nama_barang"
                        name="nama_barang"
                        component={ReanderField}
                        label="Nama Barang"
                        placeholder="Masukan Nama Barang"
                      />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        id="kadar_modal"
                        name="kadar_modal"
                        component={ReanderField}
                        label="Kadar Modal"
                        placeholder="Masukan Kadar Modal"
                      />
                    </div>
                    <div className="col-lg-8">
                      <Field
                        name="atribut"
                        component={ReanderField}
                        label="Nama Atribut"
                        placeholder="Masukan Atribut"
                      />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        name="harga_atribut"
                        component={ReanderField}
                        label="Harga Atribut"
                        {...currencyMask}
                        placeholder="Masukan Harga Atribut"
                      />
                    </div>
                    <div className="col-lg-6 d-none">
                      <Field
                        name="kadar_cetak"
                        component={ReanderField}
                        label="Kadar Cetak"
                        normalize={Gramasi}
                        placeholder="Masukan Kadar Cetak"
                      />
                    </div>
                    <div className="col-lg-4 d-none">
                      <Field
                        name="kadar"
                        component={ReanderField}
                        label="Kadar"
                        normalize={Gramasi}
                        placeholder="Masukan Kadar"
                      />
                    </div>
                    <div className="col-lg-6">
                      <Field
                        name="berat_asli"
                        component={ReanderField}
                        label="Berat Asli"
                        normalize={Gramasi}
                        placeholder="Masukan Berat"
                      />
                    </div>
                    <div className="col-lg-4 d-none">
                      <Field
                        name="berat_atribut"
                        component={ReanderField}
                        label="Berat Atribut"
                        normalize={Gramasi}
                        placeholder="Masukan Berat Atribut"
                      />
                    </div>
                    <div className="col-lg-8"></div>
                    <div className="col-lg-6">
                    <Field
                        name="is_markis"
                        onChange={(e) => {
                          if(e === "TRUE"){
                            this.props.change("berat_asli", 0)
                          }
                          this.setState({ kode_gudang: e })}}
                        component={ReanderSelect}
                        options={[
                          {
                            value :"TRUE",
                            name : "MARKIS"
                          },
                          {
                            value : "FALSE",
                            name : "NORMAL"
                          }
                        ]}
                        label="Markis"
                        placeholder="Silahkan Pilih Markis"
                      />
                    </div>
                    <div className="col-lg-6">
                      <Field
                        name="kode_intern"
                        component={ReanderField}
                        label="Kode Intern"
                        placeholder="Masukan Kode Intern"
                      />
                    </div>
                  </div>
                </div>
                <div className="col-lg-4">
                  <div className="row">
                    <div className="col-lg-1 text-center"> &nbsp; </div>
                    <div className="col-lg-4 text-center">
                      {this.props.modalwebcam === true ? (
                        this.props.setCamera === false ? (
                          <div className="container">{this.loadcamera()}</div>
                        ) : (
                          <>
                            <img
                              src={
                                this.props.foto === undefined
                                  ? this.props.setCamera === false
                                    ? imgfound
                                    : this.props.setCamera
                                  : this.props.setCamera
                                  ? this.props.setCamera
                                  : this.props.foto
                              }
                              alt="img"
                              height="245px"
                              width="300px"
                            />
                          </>
                        )
                      ) : (
                        <>
                          <img
                            src={
                              this.props.foto === undefined
                                ? this.props.setCamera === false
                                  ? imgfound
                                  : this.props.setCamera
                                : this.props.setCamera
                                ? this.props.setCamera
                                : this.props.foto
                            }
                            alt="img"
                            height="245px"
                            width="300px"
                          />
                        </>
                      )}
                    </div>
                  </div>
                  <div className="row mt-2">
                    <div className="col-lg-12 mb-8">
                      <div className="row">
                        <div className="col-6">
                          <button
                            className="btn btn-block btn-primary"
                            type="button"
                            onClick={() => this.tampilModalWebCam(this.props)}
                          >
                            {" "}
                            Ambil Foto{" "}
                          </button>
                        </div>
                        <div className="col-6">
                          <button
                            className="btn btn-block btn-warning"
                            type="button"
                            onClick={() => this.resetKamera()}
                          >
                            Reset
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12">
                <div className="row justify-content-end">
                    <div className="col-6">
                      <button
                        type="submit"
                        disabled={this.state.cekStatus}
                        className="btn btn-primary btn-block mt-1"
                      >
                        <i className="fa fa-plus"></i> Simpan{" "}
                      </button>{" "}
                    </div>
                    <div className="col-6">
                      <button
                        type="button"
                        onClick={() => this.props.kembali()}
                        className="btn btn-block btn-secondary mt-1"
                      >
                        Kembali{" "}
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </PanelBody>
          <ModalGlobal
            title={
              this.state.showModalForm === true
                ? "Tambah Data Barang Banyak"
                : "Form Data Atribut"
            }
            content={
              this.state.showModalForm === true ? (
                <TambahBarangBanyak FormBatal={() => this.FormBatal(true)} />
              ) : (
                <FormDataAtribut
                  FormBatal={() => this.FormBatal(false)}
                  onSubmit={(data) => this.simpanDataAtrr(data)}
                />
              )
            }
          />
        </Panel>
        {this.state.loadingData ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </>
    );
  }
}

FormTambahDataBarang = reduxForm({
  form: "FormTambahDataBarang",
  enableReinitialize: true,
  destroyOnUnmount: false,
  validate: ValidasiTambahBarangPusat
})(FormTambahDataBarang);
export default connect(maptostate)(FormTambahDataBarang);
