const ValidasiTambahBarangPusat = (value) => {
    const errors = {};
    // console.log(event.charCode==13);
    if (!value.berat) {
      errors.berat = "Berat Harus Diisi";
    }
    if (!value.baki) {
      errors.baki = "Baki Harus Dipilih";
    }
  
    return errors;
  };
  
  export default ValidasiTambahBarangPusat;
  