import { getMasterAtribut } from "../../../../actions/datamaster_action";
import {
  React,
  connect,
  reduxForm,
  Component,
  Field,
  ReanderField,
  ReanderSelect,
  localStoragedecryp,
} from "../../../../components/helpers/library";

const maptostate = (state) => {
  let dataattr = localStoragedecryp('dataattr');
  let dataattrjson = {};
  if (dataattr.length !== 0) {
    dataattrjson = JSON.parse(localStoragedecryp('dataattr')); 
  }
  return {
    initialValues: {
      level1: dataattrjson.level1,
      level2: dataattrjson.level2,
      level3: dataattrjson.level3,
      level4: dataattrjson.level4,
      custome: dataattrjson.custome,
      kadar: dataattrjson.kadar,
      // kadar: localStoragedecryp('kadar_type'),
      pabrik: dataattrjson.pabrik,
      model : dataattrjson.model,
    },
    model : dataattrjson.model,
    DataAtribut: state.datamaster.getMasterAtribut,
    DataPabrik: state.datamaster.getMasterPabrik,
  };
};

class FormDataAtribut extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.props.dispatch(getMasterAtribut());
  }
  render() {
    return (
      <>
        <form onSubmit={this.props.handleSubmit}  autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}>
          <div className="row">
            <div className="col-lg-3">
              <Field
                name="level1"
                component={ReanderSelect}
                options={this.props.DataAtribut
                  .filter((list) => list._id !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.nama_atribut,
                      name: list.nama_atribut
                    };
                    return data;
                  })}
                label="Level 1"
                placeholder="Silahkan Pilih Level 1"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="level2"
                component={ReanderSelect}
                options={this.props.DataAtribut
                  .filter((list) => list._id !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.nama_atribut,
                      name: list.nama_atribut
                    };
                    return data;
                  })}
                label="Level 2"
                placeholder="Silahkan Pilih Level 2"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="level3"
                component={ReanderSelect}
                options={this.props.DataAtribut
                  .filter((list) => list._id !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.nama_atribut,
                      name: list.nama_atribut
                    };
                    return data;
                  })}
                label="Level 3"
                placeholder="Silahkan Pilih Level 3"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="level4"
                component={ReanderSelect}
                options={this.props.DataAtribut
                  .filter((list) => list._id !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.nama_atribut,
                      name: list.nama_atribut
                    };
                    return data;
                  })}
                label="Level 4"
                placeholder="Silahkan Pilih Level 4"
              />
            </div>
            <div className="col-lg-6">
              <Field
                name="custome"
                component={ReanderField}
                label="Custome"
                placeholder="Masukan Custome"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="kadar"
                component={ReanderField}
                // options={[{value: localStoragedecryp('kadar_type'),name: localStoragedecryp('kadar_type')}]}
                label="Kadar"
                placeholder="Silahkan Masukan Kadar"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="pabrik"
                component={ReanderSelect}
                options={localStoragedecryp('pabrik') === '-' ? this.props.DataPabrik
                  .map((list) => {
                    let data = {
                      value: list.nama_pabrik,
                      name: list.nama_pabrik
                    };
                    return data;
                  }) : [{value: localStoragedecryp('pabrik'),name:localStoragedecryp('pabrik')}]}
                label="Pabrik"
                placeholder="Silahkan Masukan Pabrik"
              />
            </div>
            <div className="col-lg-6">
              <label>
                <Field
                  name="model"
                  component="input"
                  type="checkbox"
                  defaultChecked= {this.props.model}
                  value="1"
                /> &nbsp; Model
              </label>
            </div>
            <div className="col-lg-6 text-right">
              <button type="submit" className="btn btn-primary mt-1">
                <i className="fa fa-plus"></i> Simpan{" "}
              </button>{" "}
              &nbsp;
              <button
                type="button"
                onClick={() => this.props.FormBatal()}
                className="btn btn-warning mt-1"
              >
                Batal{" "}
              </button>{" "}
            </div>
          </div>
        </form>
      </>
    );
  }
}

FormDataAtribut = reduxForm({
  form: "FormDataAtribut",
  enableReinitialize: true,
})(FormDataAtribut);
export default connect(maptostate)(FormDataAtribut);
