import React, { lazy } from "react";
import { Link } from "react-router-dom";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import Tabel from "../../../components/helpers/tabel.jsx";
import { checkTimeout, getData, postData, postData2 } from "../../../config/axios.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import {
  setCamera,
  setFocus,
  showEditBarang,
} from "../../../actions/databarang_action.jsx";
import { hideModal, isLoading, showModal } from "../../../actions/datamaster_action.jsx";
import HeadEditDataBarang from "./EditBarang/HeadEditDataBarang.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import { reset } from "redux-form";
import { dataURLtoFile, getImage, localStoragedecryp, localStorageencryp, postImage } from "../../../components/helpers/function.jsx";
import Loading from "react-fullscreen-loading";
import FormTambahDataBarang from "./TambahBarang/FormTambahDataBarang.jsx";
const FIlterPencarianBarang = lazy(() => import("./FIlterPencarianBarang.jsx"));

const maptostate = (state) => {
  return {
    showEditBarang: state.databarang.showEditBarang,
    hideModal: state.datamaster.modalDialog,
  };
};
class DataBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      // isLoadingDataBarang: false,
      formTambahBarang: false,
      setfocus: false,
      formEdit: false,
      simpanBarang: false,
      loadingData: false,
      databarang: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.getBarangKodeBaki = this.getBarangKodeBaki.bind(this);
  }
  getBarangKodeBaki(e) {
    if(this.delayTimer){
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      localStorageencryp("databarangall", JSON.stringify([]));
      this.setState({ loadingData: true });
      if (e === "") {
        this.setState({ loadingData: false, databarang: [] });
        return false;
      }
      getData("barang/get/by-kode-baki?limit=1000&kode_baki=" + e)
        .then((res) => {
          if (res.data.length === 0) {
            this.setState({ databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Baki Yang Dicari Tidak Ada");
          } else {
            this.setState({ databarang: res.data });
            localStorageencryp("databarangall", JSON.stringify(res.data));
          }
          this.setState({ loadingData: false });
        })
        .catch((err) => {
          this.setState({ loadingData: false, databarang: [] });
          if (checkTimeout(err.message)) {
            this.setState({ loadingData: false, databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Baki Yang Dicari Tidak Ada");
          }
        });
      // postData('barang/get/by-kode-baki/', e)
      // .then((res) => {
      //   if (res.data.length === 0) {
      //     this.setState({ databarang: [] });
      //   } else {
      //     this.setState({ databarang: res.data });
      //     localStorageencryp("databarangall", JSON.stringify(res.data));
      //   }
      //   this.setState({ loadingData: false });
      // })
      // .catch((err) => {
      //   this.setState({ loadingData: false,databarang: [] });
      //   localStorageencryp("databarangall", JSON.stringify([]));
      //   ToastNotification("info", "Kode Barcode Yang Anda Dicari Tidak Ada");
      // });
    }, 1500);
  }
  showEditBarang(row) {
    // console.log(dataEdit);
    // this.setState({ isLoadingDataBarang: true });
    getImage('foto_produk/'+row.kode_barcode)
      .then((res) => {
        let dataEdit = {
          foto: res,
          kode_barcode: row.kode_barcode,
          nama_barang: row.nama_barang,
          nama_atribut: row.nama_atribut,
          harga_atribut: row.harga_atribut,
          kode_intern: row.kode_intern,
          kadar: row.kadar,
          kadar_cetak: row.kadar_cetak,
          tipe_barang: row.tipe_barang,
        };
        this.props.dispatch(showEditBarang(dataEdit));
        setTimeout(() => {
          // this.setState({ isLoadingDataBarang: false });
          this.props.dispatch(showModal());
        }, 500);
      })
      .catch((err) => {
        let dataEdit = {
          kode_barcode: row.kode_barcode,
          nama_barang: row.nama_barang,
          nama_atribut: row.nama_atribut,
          harga_atribut: row.harga_atribut,
          kode_intern: row.kode_intern,
          kadar: row.kadar,
          kadar_cetak: row.kadar_cetak,
        };
        this.props.dispatch(showEditBarang(dataEdit));
        setTimeout(() => {
          // this.setState({ isLoadingDataBarang: false });
          this.props.dispatch(showModal());
        }, 500);
      });
  }
  getKodeBarcode(e) {
    if(e.length >= 8){

    if(this.delayTimer){
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      localStorageencryp("databarangall", JSON.stringify([]));
      this.setState({ loadingData: true });
      if (e === "") {
        this.setState({ loadingData: false, databarang: [] });
        return false;
      }
      getData("barang/open/kode-barcode?limit=1000&kode_barcode=" + e)
        .then((res) => {
          // console.log(res.data);
          if (res.data.length === 0) {
            this.setState({ databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Barcode Yang Dicari Tidak Ada");
          } else {
            this.setState({ databarang: res.data });
            localStorageencryp("databarangall", JSON.stringify(res.data));
          }
          this.setState({ loadingData: false });
        })
        .catch((err) => {
          this.setState({ loadingData: false, databarang: [] });
          if (checkTimeout(err.message)) {
            this.setState({ loadingData: false, databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Barcode Yang Dicari Tidak Ada");
          }
        });
    }, 1500);
  }

  }
  getKodeBarcodenoTimout(e) {
    localStorageencryp("databarangall", JSON.stringify([]));
    this.setState({ loadingData: true });
    if (e === "") {
      this.setState({ loadingData: false, databarang: [] });
      return false;
    }
    getData("barang/open/kode-barcode?limit=1000&kode_barcode=" + e)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          this.setState({ databarang: [] });
          localStorageencryp("databarangall", JSON.stringify([]));
          ToastNotification("info", "Kode Barcode Yang Dicari Tidak Ada");
        } else {
          this.setState({ databarang: res.data });
          localStorageencryp("databarangall", JSON.stringify(res.data));
        }
        this.setState({ loadingData: false });
      })
      .catch((err) => {
        this.setState({ loadingData: false, databarang: [] });
        if (checkTimeout(err.message)) {
          this.setState({ loadingData: false, databarang: [] });
          localStorageencryp("databarangall", JSON.stringify([]));
          ToastNotification("info", "Kode Barcode Yang Dicari Tidak Ada");
        }
      });
  }
  getNamaBarang(e) {
    if(this.delayTimer){
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      localStorageencryp("databarangall", JSON.stringify([]));
      this.setState({ loadingData: true });
      if (e === "") {
        this.setState({ loadingData: false, databarang: [] });
        return false;
      }
      getData("barang/get/by-nama-barang?limit=1000&nama_barang=" + e)
        .then((res) => {
          // console.log(res.data);
          if (res.data.length === 0) {
            this.setState({ databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Nama Barang Yang Dicari Tidak Ada");
          } else {
            this.setState({ databarang: res.data });
            localStorageencryp("databarangall", JSON.stringify(res.data));
          }
          this.setState({ loadingData: false });
        })
        .catch((err) => {
          this.setState({ loadingData: false, databarang: [] });
          if (checkTimeout(err.message)) {
            this.setState({ loadingData: false, databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Nama Barang Yang Dicari Tidak Ada");
          }
        });
    }, 1500);
  }
  showFormTambahDataBarang() {
    this.setState({
      formTambahBarang: true,
    });
  }
  getByKodeGroup(e) {
    if(this.delayTimer){
      clearTimeout(this.delayTimer);
    }
    this.delayTimer = setTimeout(() => {
      localStorageencryp("databarangall", JSON.stringify([]));
      this.setState({ loadingData: true });
      if (e === "") {
        this.setState({ loadingData: false, databarang: [] });
        return false;
      }
      getData("barang/get/by-kode-group?limit=1000&kode_group=" + e)
        .then((res) => {
          // console.log(res.data);
          if (res.data.length === 0) {
            this.setState({ databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Group Yang Dicari Tidak Ada");
          } else {
            this.setState({ databarang: res.data });
            localStorageencryp("databarangall", JSON.stringify(res.data));
          }
          this.setState({ loadingData: false });
        })
        .catch((err) => {
          this.setState({ loadingData: false, databarang: [] });
          if (checkTimeout(err.message)) {
            this.setState({ loadingData: false, databarang: [] });
            localStorageencryp("databarangall", JSON.stringify([]));
            ToastNotification("info", "Kode Group Yang Dicari Tidak Ada");
          }
        });
    }, 1500);
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
   
    // console.log(data.foto);
    let hasil = {
      nama_barang: data.nama_barang,
      nama_atribut: data.nama_atribut || "-",
      harga_atribut: data.harga_atribut || 0,
      kode_intern: data.kode_intern,
      kadar: data.kadar,
      kadar_cetak: data.kadar_cetak,
      tipe_barang: data.tipe_barang,
    };

    const file = data.foto === undefined ? data.foto_lama  : dataURLtoFile(data.foto)
   

    postData("barang/edit/" + data.kode_barcode, hasil)
      .then(() => {
        // document.getElementById('kode_barcode').focus()
        this.props.dispatch(reset("FIlterPencarianBarang"))
        this.getKodeBarcodenoTimout(data.kode_barcode)
        this.props.dispatch(setFocus(true));
        this.props.dispatch(hideModal());
      })
      .then(() => {
        if(data.foto !== undefined){
          postImage(file,'foto_produk/'+data.kode_barcode).then((res)=>{
          }).catch((err)=>{
            ToastNotification('info', "upload Foto Gagal, Silahkan Edit Didata Barang")
          })
        }
      })
      .then(() => {
        this.setState({ databarang: [] });
        this.props.dispatch(reset("HeadEditDataBarang"));
        this.props.dispatch(showEditBarang([]));
        this.props.dispatch(setCamera(false));
      })
      .then(() => {
        ToastNotification("success", "Data Berhasil Disimpan");
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => {
        this.setState({ isLoading: false });
        ToastNotification("info", "Barang Gagal Di Edit");
      });
  }

  simpanDataBarang(data) {
    if(data.berat_asli=== undefined){
      ToastNotification("info", "Berat Harus Diisi");
      return false;
    }
    this.props.dispatch(isLoading(true));
   
    this.setState({
      simpanBarang : true
    })
    let tipe_barnag = localStoragedecryp("kadar_type");

    const file = data.foto !== undefined  ? dataURLtoFile(data.foto) : undefined ;
    let hasil = {
      kode_group: data.kode_group || '-',
      kode_dept: data.kode_jenis || "-",
      kode_gudang: data.gudang || "-",
      kode_toko: data.baki || "-",
      kode_baki_label: "-",
      kode_intern: data.kode_intern || "-",
      nama_barang: data.nama_barang || "-",
      berat: parseFloat(data.berat_asli) || 0,
      berat_asli: parseFloat(data.berat_asli) || 0,
      kadar_modal: parseFloat(data.kadar_emas) || 0,
      kadar: parseInt(100) || 0,
      kadar_cetak: data.kadar_cetak || "0",
      nama_atribut: data.atribut || "-",
      tipe_barang: tipe_barnag || "SP",
      berat_atribut: parseFloat(data.berat_atribut) || 0,
      berat_plastik: 0,
      berat_susut: 0,
      berat_cad: 0,
      tujuan: '-',
      harga_atribut: data.harga_atribut || 0,
      tag_id: data.stock_input || "-",
      berat_bandrol: 0,
      no_pesanan: "-",
      id_barang_pesanan: "-",
      no_kirim_bos: "-",
      qty_input: parseInt(data.jumlah_barang) || 1,
      is_markis: data.is_markis === "TRUE"
    };
   
    postData2("tambah-barang/create", hasil)
      .then((res) => {
        for(let kode of res.data.kode_barcode){
          if(file !== undefined){
            postImage(file,'foto_produk/'+kode).then((res)=>{
            }).catch((err)=>{
              ToastNotification('info', "upload Foto Gagal, Silahkan Edit Didata Barang")
            })
          }
        }

        ToastNotification("success", res.data.message);
        this.props.dispatch(reset("FormTambahDataBarang"))
      })
      .then(() => {
        this.props.dispatch(isLoading(false));
        this.props.dispatch(setCamera(false));
        this.props.dispatch(reset("FormDataAtribut"));
        localStorage.removeItem("dataattr")
        // window.location.reload();
        this.setState({
          simpanBarang : false
        })
      })

      .catch((error) => {
        this.props.dispatch(isLoading(false));
        this.setState({
          simpanBarang : false
        })
        ToastNotification("info", error.response?.data.message || "Terjadi Kesalhaan Saat Mengirim Data");
      });
  }
  kembali() {
    this.setState({
      formTambahBarang: false,
    });
  }
  render() {
    let columns = [
      // {
      //   dataField: "kode_barang",
      //   text: "Kode Barang ",
      //   sort: true,
      // },
      {
        dataField: "kode_barcode",
        text: "Kode Barcode",
      },
      // {
      //   dataField: "tipe_barang",
      //   text: "Tipe Barang",
      // },
      {
        dataField: "kode_group",
        text: "Kode Group",
      },
      {
        dataField: "kode_toko",
        text: "Kode Baki",
      },
      {
        dataField: "kode_dept",
        text: "Kode Jenis",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses : "text-right",
        formatter: (row) => {
          return <div className="text-right"> {row ===  null || undefined  ? row : row.toFixed(3)} </div>
        }
      },
      {
        dataField: "input_date",
        text: "Tgl Input",
      },
      {
        dataField: "action",
        text: "Action",
        csvExport: false,
        headerClasses: "text-center",
        formatter: (rowcontent, row) => {
          return (
            <div className="row text-center">
              <div className="col-12">
                <button
                  type="button"
                  onClick={() => this.showEditBarang(row)}
                  className="btn btn-primary mr-3"
                >
                  <i className="fa fa-edit"></i>
                </button>
              </div>
            </div>
          );
        },
      },
    ];
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Nama Atribut : {row.nama_atribut} <br />
              Betat Atribut : {row.berat_atribut} <br />
              Harga Atribut : {row.harga_atribut} <br />
              Kode Intern : {row.kode_intern} <br />
              Kadar : {row.kadar} <br />
              Kadar Cetak : {row.kadar_cetak} <br />
              Kode Toko : {row.kode_toko} <br />
              Kode Gudang : {row.kode_gudang} <br />
              Stock : {row.stock_on_hand} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">
            {this.state.formTambahBarang === true
              ? "Tambah Data Barang"
              : "Data Barang"}{" "}
          </li>
        </ol>
        <h1 className="page-header">
          {this.state.formTambahBarang === true
            ? "Tambah Data Barang"
            : "Data Barang"}{" "}
        </h1>
        {this.state.formTambahBarang === true ? (
          <FormTambahDataBarang
            kembali={() => this.kembali()}
            isLoading={this.state.isLoading}
            onSubmit={(data) => this.simpanDataBarang(data)}
          />
        ) : (
          <>
            <Panel>
              <PanelHeader>Filter Pencarian Barang</PanelHeader>
              <br />
              {/* {console.log(this.state.setfocus)} */}
              <PanelBody>
                <FIlterPencarianBarang
                  getKodeBarcode={(data) => this.getKodeBarcode(data)}
                  getNamaBarang={(data) => this.getNamaBarang(data)}
                  getByKodeGroup={(data) => this.getByKodeGroup(data)}
                  getBarangKodeBaki={(data) => this.getBarangKodeBaki(data)}
                />
              </PanelBody>
            </Panel>
            <Panel>
              <PanelHeader>
                {this.state.loadingData ? (
                  <>
                    {" "}
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari Data Barang{" "}
                  </>
                ) : (
                  "Data Barang"
                )}
              </PanelHeader>
              <br />

              {/* {console.log(localStorage.getItem('databarangall'))} */}
              {/* Data Barang */}
              <PanelBody>
                <Tabel
                  btnText="Tambah Data Barang"
                  handleClick={() => this.showFormTambahDataBarang()}
                  tambahData={true}
                  keyField="kode_barang"
                  exportCsv={true}
                  data={this.state.databarang}
                  columns={columns}
                  empty={true}
                  textEmpty={
                    this.state.loadingData ? (
                      <>
                        {" "}
                        <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                        Mencari Data Barang
                      </>
                    ) : (
                      "Data Barang Kosong"
                    )
                  }
                  expandRow={expandRow}
                />
              </PanelBody>

              <br />
              {/* End Data Barang */}
              <ModalGlobal
                title="Edit Barang"
                content={
                  <HeadEditDataBarang
                    isLoading={this.state.isLoading}
                    onSubmit={(data) => this.handleSubmit(data)}
                  />
                }
              />
              {/* End Tambah Data Barang  */}
            </Panel>
          </>
        )}
        {this.state.loadingData ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
        {this.state.simpanBarang ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </div>
    );
  }
}

export default connect(maptostate, null)(DataBarang);
